<template>
    <div class="table-responsive" v-if="games && games.length">
        <table class="bg-games mb-3 mb-xl-5 animated fadeIn" >
            <tr class="match-row match-row_headings d-none d-sm-table-row">
                <td class="pr-1 pl-4"></td>
                <td class="pt-3 pb-2 px-1 d-none d-xxl-table-cell">
                    <div class="match-heading">
                        Odds
                    </div>
                </td>
                <td class="pt-3 pb-2 pl-1 pr-4">
                    <div class="match-heading">All</div>
                </td>
            </tr>
            <tr class="match-row pb-2 pb-sm-0"
                v-for="(game, gIndex) in games"
                :class="{'odd': gIndex % 2}"
                :key="Math.random() * 100 + gIndex"
                v-if="!$gameHelper.isStarted(game)">
                <td class="match-info text-left pt-4 pb-1 py-sm-3 pr-1 pl-4">
                    <ul class="match">
                        <li class="date pr-1">
                            <span class="date__time">{{ displayTime(game.scheduled)}}</span>
                            <br>
                            <span class="date__day">{{dateName(game.scheduled)}}</span>
                            <span class="id">ID: {{ game.game_id }}</span>
                        </li>
                        <li class="home-team pr-sm-1">{{game.home_team}}</li>
                    </ul>
                </td>
                <td class="match-bets match-bets_1x2 py-2 py-sm-3 px-1" style="border-right: #263e77 1px solid !important;">
                    <div class="match-header d-sm-none">
                        {{ game.markets[0].name }}
                    </div>
                    <ul class="name">
                        <li class="mx-1 p-1" :class="[isSelected(game, odd.id) ? 'picked': '', `slip${odd.id}`]"
                            v-for="(odd, oddIndex) in game.markets[0].odds.slice(1, 8)"
                            :key="oddIndex"
                            @click="() => { isSelected(game, odd.id) ? removeFromBetSlip(game, odd.id) : addToBetSlip(game,odd,odd.id)}" style="cursor: pointer">
                            <span> {{odd.name}} <br> {{ odd.value}} </span>
                        </li>
                    </ul>
                </td>
                <td class="match-bets match-bets_all pt-4 py-sm-3 pl-1 pr-4">
                    <div class="match-header d-sm-none">All</div>
                    <div class="name">
              <span>
                  <a class="more-markets"
                     :class="gameIsSelected(game.game_id) ? 'picked': ''"
                     style="cursor: pointer;"
                     @click.prevent="loadMoreMarkets(game)">
                    +{{ game.markets.length }}
                  </a>
              </span>
                        <!-- <br>
                        <span @click="loadStatistics()">
                           <i class="fa fa-chart-bar"></i>
                      </span> -->
                    </div>
                </td>
            </tr>
        </table>
    </div>
</template>

<script>
    import moment from 'moment'
    import {mapGetters} from 'vuex'
    import { Bus } from '../../EventBus'
    export default {
        props: ['games'],
        name: 'MotorSportGameComponent',
        computed: {
            ...mapGetters(['betSlip'])
        },
        methods : {
            removeFromBetSlip (game) {
                Bus.$emit('gameRemoved')
                this.$store.dispatch('removeFromSlip', game)
            },
            addToBetSlip (game, odd, pick) {
                console.log('SHOWING LOCAL BET SLIP', this.gamesInBetSlip)
                Bus.$emit('gameAdded')
                this.$store.dispatch('updateBetSlip', {game: game, odd: odd})
                this.isSelected(game, pick)
            },

            hasOdds (game) {
                let isOdds = false
                game.forEach((odds) => {
                    if (odds.value >= 1.01) {
                        isOdds = true
                    }
                })
                return isOdds
            },
            isSelected (game, pick) {
                let isSelected = false
                if (this.betSlip) {
                    this.betSlip.filter(item => {
                        if (item.gameId === game.game_id && item.oddId === pick) {
                            isSelected = true
                        }
                    })
                    return isSelected
                }
            },
            gameIsSelected (gameId) {
                let isSelected = false
                if (this.betSlip) {
                    this.betSlip.filter(item => {
                        if (item.gameId === gameId && item.odd.market_name !== 'Match Result') {
                            isSelected = true
                        }
                    })
                    return isSelected
                }
            },
            loadMoreMarkets (game) {
                // Set the more market game to local storage
                this.$store.dispatch('getGameMarkets', game)
                this.$router.push({name: 'more-markets'})
            },
            fullTime (scheduled) {
                return moment(scheduled).format('HH:mm')
            },
            dateName (scheduled) {
                return moment.utc(scheduled).local().format('ddd, MMM Do')
            },
            displayTime (scheduled) {
                return moment.utc(scheduled).local().format('HH:mm')
            },
            mobileDate (scheduled) {
                return moment.utc(scheduled).local().format('ddd')
            }
        }
    }
</script>
