<template>
  <div class="card rounded-0">
    <div class="d-block d-md-none">
      <LandingButtons :onLeagues="true" :league_title="title"></LandingButtons>
    </div>
    <div class="landing__main card-body p-0">
      <div class="text-center">
        <loader v-if="isLoading"></loader>
        <no-games v-if="!isLoading && games && !games.length"></no-games>
      </div>
      <games :games="games" v-if="!mortoSport"></games>
      <MotorSportGameComponent v-else :games="games"></MotorSportGameComponent>
    </div>
  </div>

</template>

<script>
  import {TOURNAMENT_URL} from '../../api_routes'
  import axios from 'axios'
  import UpcomingSoccer from "./UpcomingGames";
  import MotorSportGameComponent from "./MotorSportGameComponent";

  const Games = () => import('./GamesComponent.vue');
  const Loader = () => import('./../Utilities/LoadingIndicator');
  const NoGames = () => import('./../Utilities/NoGames');
  const LandingButtons = () => import("../Utilities/LandingButtons");
  export default {
    name: 'LeagueGames',
    components: {MotorSportGameComponent, UpcomingSoccer, Games, Loader, NoGames, LandingButtons},
    props: ['isGroup'],
    data () {
      return {
        games: [],
        fetching: true,
        upcoming: false,
        isHighlight: true,
        isLoading: false,
        title: '',
        mortoSport: false
      }
    },
    computed: {},
    mounted () {
      this.getLeagueGames();
      this.title = this.$route.params.league_name
    },
    watch: {
      '$route.params.league_id' () {
        this.getLeagueGames(this.$route.params.league_id);
        this.games = [];
        this.mortoSport = false
      },
      '$route.params.league_name' () {
        this.title = this.$route.params.league_name
      }
    },
    methods: {
      getLeagueGames (id) {
        this.isLoading = true;
        id = this.$route.params.league_id;
        axios.get(TOURNAMENT_URL + id).then((resp) => {
          if (resp.status === 200) {
            let responseData = resp.data.data;
            if (responseData && responseData.length && parseInt(responseData[0].sport_id) === 16) {
                this.mortoSport = true;
            }
            this.games = responseData;
            this.isLoading = false
          }
        }, () => this.isLoading = false )
      }
    }
  }
</script>

